import React from "react";
import html2canvas from "html2canvas";
import { Link } from "react-router-dom";

const makePNG = () => {
  var printCanvas = document.getElementsByClassName("Canvas")[0];
  /*
var designCanvas = document.getElementsByClassName("Canvas")[0];
var printCanvas = designCanvas.cloneNode(true);
printCanvas.style.transform = "scale(2.5)";
printCanvas.style.position = "absolute";
document.body.prepend(printCanvas)
/* */

  var time = new Date().valueOf();
  var pngName = `M&M-custom-${time}.png`;

  function renderError(error) {
    console.log(error);
  }

  function renderComplete(canvas) {
    let file = canvas.toDataURL("image/png");
    var download = document.createElement("a");
    download.href = file;
    download.download = pngName;
    download.click();
    //printCanvas.remove()
  }

  html2canvas(printCanvas, {
    useCORS: true,
    scale: 2,
    dpi: 150,
    timeout: 15000,
    allowTaint: true,
    backgroundColor: null,
    height: printCanvas.offsetHeight,
    width: printCanvas.offsetWidth,
    
    onclone: (el)=>{

      const elText = el.querySelectorAll('.nameBox');
      for(let i = 0; i<elText.length; i++){
        const range = elText[i].ownerDocument.createRange();
        range.setStart(elText[i].childNodes[0], 0);
        range.setEnd(elText[i].childNodes[0], 1);
        console.log(range.getBoundingClientRect())
        const fontSize   = parseInt(elText[i].style.fontSize,10);
        const lineHeight = parseInt(elText[i].style.lineHeight,10);
        const oriRect    = elText[i].getBoundingClientRect();
        const rangRect   = range.getBoundingClientRect();
        const oriTextTop = oriRect.top + (lineHeight - fontSize)/2
        const rangeTextTop = rangRect.bottom - fontSize;
        console.log({fontSize,lineHeight,oriRect,rangRect,oriTextTop,rangeTextTop})
        elText[i].style.top = `${parseInt(elText[i].style.top, 10)-(rangeTextTop-oriTextTop)}px`
      }
      /*
      for(let i = 0; i<elText.length; i++){
        const range = elText[].ownerDocument.createRange();
        range.setStart(elText.childNodes[0], 0);
        range.setEnd(elText.childNodes[0], 1);
        const fontSize = parseInt(elText.style.fontSize,10);
        const lineHeight = parseInt(elText.style.lineHeight,10);
        const oriRect = elText.getBoundingClientRec();
        const rangRect = range.getBoundingClientRec();
        const oriTextTop = oriRect.top + (lineHeight - fontSize) /2
        const rangeTextTop = rangRect.bottom - fontSize;
        elText.style.top = `${parseInt(elText.style.top, 10)-(rangeTextTop-oriTextTop)}px`
      }
      */
    }
  }).then(renderComplete)
    .catch(renderError);

  console.log("click");
};

export const BtnMakePdf = () => (
  <div className="BTN" onClick={makePNG}>
    <b>Save Image</b>
  </div>
);

export const BtnBack = () => (
  <Link to="/" className="BTN">
    <b>Back</b>
  </Link>
);
